import { Injectable } from '@angular/core';
import {
  RecordInstance,
  RX_RECORD_DEFINITION,
  RxRecordInstanceService,
  RxRecordInstanceUpdateService
} from '@helix/platform/record/api';
import { forkJoin, Observable } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { map } from 'lodash';
import { RX_CHATBOTS } from './chatbots.constant';
import { IBundleDescriptor } from '@helix/platform/shared/api';

@Injectable({
  providedIn: 'root'
})
export class RxChatbotDefinitionService {
  constructor(
    private rxRecordInstanceService: RxRecordInstanceService,
    private rxRecordInstanceUpdateService: RxRecordInstanceUpdateService
  ) {}

  delete(recordInstanceIds: string[]): Observable<any[]> {
    return forkJoin(
      map(recordInstanceIds, (id) => {
        return this.rxRecordInstanceService.delete(RX_CHATBOTS.chatbotSettings.recordDefinitionName, id);
      })
    );
  }

  create(chatbotName: string, bundleDescriptor: IBundleDescriptor): Observable<any> {
    return this.rxRecordInstanceService.getNew(RX_CHATBOTS.chatbotSettings.recordDefinitionName).pipe(
      switchMap((recordInstance: RecordInstance) => {
        recordInstance.setFieldValue(RX_CHATBOTS.chatbotSettings.recordFieldIds.name, chatbotName);
        recordInstance.setFieldValue(RX_CHATBOTS.chatbotSettings.recordFieldIds.bundleId, bundleDescriptor.id);

        recordInstance.setFieldValue(
          RX_RECORD_DEFINITION.coreFieldIds.description,
          `${bundleDescriptor.friendlyName} Chatbot configuration`
        );

        return this.rxRecordInstanceService.create(recordInstance);
      })
    );
  }

  rename(recordInstanceId: string, newChatbotName: string): Observable<any> {
    return this.rxRecordInstanceService.get(RX_CHATBOTS.chatbotSettings.recordDefinitionName, recordInstanceId).pipe(
      switchMap((recordInstance: RecordInstance) => {
        recordInstance.setFieldValue(RX_CHATBOTS.chatbotSettings.recordFieldIds.name, newChatbotName);

        return this.rxRecordInstanceUpdateService.execute(recordInstance);
      })
    );
  }
}
